    margin: .24rem auto .04rem;
  }
  h3 {
    line-height: .22rem;
    font-size: .16rem;
  }
  p {
    margin: .12rem 0;
    font-size: .14rem;
    line-height: .2rem;
  }
}
.cont2 {
  h3 {
    margin: .24rem 0 .1rem;
    margin-left: .2rem;
  }
  p {
    padding-left: .2rem;
    font-size: .12rem;
    line-height: .16rem;
    margin-bottom: .16rem;
  }
}

</style>


